<template>
  <div
    :class="[ $style.accordion, variantStyle ]"
  >
    <div
      v-bind="buttonProperties"
      :class="$style.header"
      role="button"
      tabindex="0"
      @click="toggle"
      @keydown.enter="toggle"
      @keydown.space="toggle"
      ref="button"
    >
      <!-- @slot Use for header content -->
      <slot
        :toggle="toggle"
        name="header"
        :open="show"
      >
        <span
          :class="$style['font-weight-bold']"
        >
          {{ header }}
        </span>
      </slot>
      <BaseIcon
        icon="chevron-down"
        :legacy="true"
        :class="{
          [$style.rotate]: show,
          [$style.icon]: true
        }"
      />
    </div>
    <transition
      name="accordion"
      @before-enter="beforeEnter"
      @enter="enter"
      @before-leave="beforeLeave"
      @leave="leave"
    >
      <div
        v-show="show"
        :class="$style.body"
        ref="body"
        v-bind="regionProperties"
      >
        <div :class="$style['body-inner']">
          <!-- @slot Body content -->
          <slot :expand="expand"/>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import BaseIcon from '../atoms/BaseIcon'

/**
 * Comment for entire component
 */
export default {
  components: {
    BaseIcon
  },
  props: {
    /**
     * Should accordion start as open?
     */
    startOpen: {
      type: Boolean,
      default: false
    },
    /** The style variant to use. */
    variant: {
      type: String,
      default: 'primary',
      validator: (value) => [ 'primary', 'secondary' ].includes(value)
    },
    /** Header text. Will be ignored if a header slot is used */
    header: {
      type: String,
      default: 'Advanced Options'
    },
    /** Body text. Will be ignored if default slot is used */
    body: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      show: false,
      mutationObserver: null,
      accordionID: null,
      ariaInit: true
    }
  },
  created: function() {
    this.show = this.startOpen
  },
  computed: {
    variantStyle() {
      return {
        [this.$style.primary]: this.variant !== 'secondary',
        [this.$style.secondary]: this.variant === 'secondary',
      }
    },
    buttonProperties () {
      return {
        id: this.accordionID,
        'aria-expanded' : 'false'
      }
    },
    regionProperties () {
      return {
        'role' : 'region',
        'aria-labelledby' : this.accordionID,
        'aria-hidden' : 'true'
      }
    }
  },
  methods: {
    toggle: function() {
      this.show = !this.show
      this.$emit('input', this.show)
      const button = this.$refs.button
      const divRegion = this.$refs.body

      if (this.show) {
        button.setAttribute('aria-expanded', 'true')
        divRegion.setAttribute('aria-hidden', 'false')
      } else {
        button.setAttribute('aria-expanded', 'false')
        divRegion.setAttribute('aria-hidden', 'true')
      }
    },
    collapse(el){
      el = el || this.$refs.body
      el.style.maxHeight = '0'
    },
    async expand(el){
      el = el || this.$refs.body
      await this.$nextTick()
      el.style.maxHeight = el.scrollHeight + 'px'
    },
    // enter: function(el, done) {
    //   $(el).slideDown(150, done);
    // },
    // leave: function(el, done) {
    //   $(el).slideUp(150, done);
    // },
    beforeEnter: function(el) {
      this.collapse(el)
    },
    enter: function(el) {
      this.expand(el)
    },
    beforeLeave: function(el) {
      this.expand(el)
    },
    leave: function(el) {
      this.collapse(el)
    },
    handleChange(){
      if(this.show){
        this.expand()
      } else {
        this.collapse()
      }
    },

  },
  mounted: function () {
      this.mutationObserver = new MutationObserver(this.handleChange)
      this.mutationObserver.observe(this.$refs.body, { attributes: true, childList: true, subtree: true })
      this.accordionID = 'accordion' + this._uid + 'id'
  },
  destroyed: function() {
      this.mutationObserver.disconnect()
  },

}

</script>

<style lang="scss" module>
