<template>
<div
  :class="$style.checkboxContainer"
>
  <div
    :class="[$style['custom-checkbox'], disabled ? $style['disabled'] : '']"
    @click="toggleInput"
  >
  <div
    :class="$style.checkMark"
  >
    <BaseIcon
      role="checkbox"
      :aria-checked="shouldBeChecked"
      :aria-disabled="disabled"
      :icon="checkMark"
      tabindex="0"
      :legacy="true"
      @keypress.native.enter="toggleInput"
      @keypress.native.space="toggleInput"
      :aria-describedby="description"
      :aria-label="label"
      :id="id"
    />
  </div>
  <input
    type="checkbox"
    ref="input"
    :class="$style['native-checkbox']"
    :name="name"
    :checked="shouldBeChecked"
    :disabled="disabled"
    @change="updateInput"
    aria-hidden="true"
  />
    <label :for="id" :class="$style['checkbox-label']"> {{ label }} </label>

  </div>
  <div
    :id="description"
    :class="$style['help-text']"
    v-show="description"
  >
  <!-- @slot Use this to include helper text/markup. -->
    <slot></slot>
  </div>
</div>
</template>

<script>
  import { v4 as uuid } from "uuid"
  import BaseIcon from '../atoms/BaseIcon'

/**
 * TODO: establish stacked and inline spacing
 * OSU styled checkbox component. Accepts an array of options to render as
 */
  export default {
    components: {
      BaseIcon
    },
    model: {
      prop: 'modelValue',
      event: 'change'
    },
    props: {
      value: {
        default: true
      },
      /**
       * Displays a label for the checkbox.
       */
      label: {
        type: String,
      },
      /**
       * Set to true to disable the input.
       */
      disabled: {
        default: false
      },
      falseValue: {
        default: false
      },
      modelValue: {
        default: false
      },
      name: {
        type: String,
        default: null
      },
    /** ID value to use for main input */
    id: {
      type: String,
      default: () => ("input" + uuid())
    },
    /** ID value to use for input help text*/
    helpId: {
      type: String,
      default: () => ("input" + uuid())
    },
    },
    computed: {
      description(){
        return this.$slots.default ? this.helpId : false
      },
      shouldBeChecked(){
        if(this.modelValue instanceof Array){
          return this.getOptionIndex() > -1
        }
        return this.modelValue === this.value
      },
      checkMark(){
        if(this.disabled && !this.value) return 'checkbox-disabled'

        return this.shouldBeChecked ? 'checkbox-selected' : 'checkbox-deselected'
      }
    },
    methods: {
      toggleInput(){
        const checkbox = this.$refs.input
        checkbox.click()
      },
      getOptionIndex() {
        let index = -1;

        this.modelValue.forEach( (item, i) => {
          if (JSON.stringify(this.value) === JSON.stringify(item)) index = i
        })

        return index

      },
      updateInput(event) {
          event.stopPropagation()
        let isChecked = event.target.checked

        // eslint-disable-next-line no-console

        if (this.modelValue instanceof Array) {
          let newValue = [...this.modelValue]
          let index = this.getOptionIndex()

          if (isChecked) {
            newValue.push(this.value)
          } else {
            if(index > -1) newValue.splice(index, 1)
          }

          this.$emit('change', newValue)

        } else {
          this.$emit('change', isChecked ? this.value : this.falseValue)
        }
      }
    }
  }
</script>

<style lang="scss" module>
  .help-text {
    font-family: $font-heading;
    line-height: $line-height-s;
    color: $color-gray;
    font-style: italic;
    @include stack-space($space-s);
  }

  .form-group {
    @include inset-space($space-s);
  }

  .custom-checkbox {
    font-family: $font-heading;
    padding-right: $space-m;
    display:flex;
    align-items: center;
  }

  .native-checkbox {
    display: none;
  }


  .checkMark {
    @include inset-squish-space($space-m);
    margin-left: $space-m * -1;
    line-height: 0;

    svg { width: 1em; }
  }

  .checkbox-icon {
    font-family: $font-heading;
    flex-shrink: 0;
  }

  .checkbox-label {
    margin:0;
  }
  .disabled {
    color: $color-gray-disabled;
    .checkbox-icon {
      opacity: $opacity-disabled;
    }
  }
</style>


